*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background: rgb(168, 235, 255);
    color: black;
}

.book-list{
    width: 90vw;
    max-width: 1170px;
    margin: 5rem auto;
    display: grid;
    gap: 2rem;
}

.book{
    background: white;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    cursor: pointer;
    position: relative;
}

.book img{
    width: 100%;
    object-fit: cover;
}

.book h2{
    margin-top: 1rem;
    font-size: 1rem;
}

.book p{
    margin: 5px;
    padding: 5px;
}

.book button{
    width: 90%;
    padding: 5px;
}

@media screen and (min-width: 400px) {
    .book-list{
        grid-template-columns: repeat(2, 1fr);

    }

}

.number{
    position: absolute;
    top: 0;
    left: 0;
    /* margin: 10px; */
    font-size: 1rem;
    padding: 1rem;
    background-color: greenyellow;
    color: rgb(0, 0, 0);
    border-top-left-radius: 1rem;
    border-bottom-right-radius: 1rem;
}

h1{
    text-align: center;
    font-size: xx-large;
    text-transform: capitalize;
    margin-top: 3rem;
}
